.form {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -170px 0 0 -170px;
    width: 320px;
    height: 300px;
    padding: 36px;
    box-shadow: 0 0 100px rgba(0, 0, 0, .08);
}

.form button {
    width: 100%;
}

.form p {
    color: rgb(204, 204, 204);
    text-align: center;
    margin-top: 16px;
    font-size: 12px;
    display: flex;
    justify-content: space-between;
}

.loginlogo {
    text-align: center;
    cursor: pointer;
    margin-bottom: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loginlogo img {
    width: 40px;
    margin-right: 8px;
}

.loginlogo span {
    vertical-align: text-bottom;
    font-size: 16px;
    text-transform: uppercase;
    display: inline-block;
    font-weight: 700;
    /*color: @primary-color;*/
}

.ant-menu-horizontal {
    border-bottom: 0px;
}

.ant-menu-horizontal > .ant-menu-item {
    border-top: 2px solid transparent;
    border-bottom: 0px;
}

.ant-menu-horizontal > .ant-menu-item:hover {
    border-top: 2px solid #1890ff;
    border-bottom: 0px;
}

.ant-menu-horizontal > .ant-menu-item-active {
    border-top: 2px solid #1890ff;
    border-bottom: 0px;
}

.ant-menu-horizontal > .ant-menu-item-selected {
    border-top: 2px solid #1890ff;
}

.ant-menu-horizontal > .ant-menu-item-selected{
    border-bottom: 0px;
}



.container {
    max-width: 600px;
    margin: 0 auto;
    min-height: 100%;
    background: white;
}

header {
    background: #d2edf4;
    background-image: linear-gradient(to bottom, #d0edf5, #e1e5f0 100%);
    padding: 20px 15px 15px 15px;
    position: relative;
}

h1 {
    font-size: 1.5em;
    margin: 0;
    margin-bottom: 10px;
    display: inline-block;
    margin-right: 1em;
}

form {
    margin-top: 10px;
    margin-bottom: -10px;
    position: relative;
}

.new-task input {
    box-sizing: border-box;
    padding: 10px 0;
    background: transparent;
    border: none;
    width: 100%;
    padding-right: 80px;
    font-size: 1em;
}

.new-task input:focus{
    outline: 0;
}

ul {
    margin: 0;
    padding: 0;
    background: white;
}

.delete {
    float: right;
    font-weight: bold;
    background: none;
    font-size: 1em;
    border: none;
    position: relative;
}

li {
    position: relative;
    list-style: none;
    padding: 15px;
    border-bottom: #eee solid 1px;
}

li .text {
    margin-left: 10px;
}

li.checked {
    color: #888;
}

li.checked .text {
    text-decoration: line-through;
}

li.private {
    background: #eee;
    border-color: #ddd;
}

header .hide-completed {
    float: right;
}

.toggle-private {
    margin-left: 5px;
}

@media (max-width: 600px) {
    li {
        padding: 12px 15px;
    }

    .search {
        width: 150px;
        clear: both;
    }

    .new-task input {
        padding-bottom: 5px;
    }
}
